.news {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__frame {
    @include frame('rect', 75%);
    @include frame('zoom');
  }

  &__body {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding-top: 16px;
    position: relative;
    z-index: 10;
  }

  &__time {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 67px;
    height: 60px;
    background-color: $primary;
    color: $white;
    position: absolute;
    bottom: 100%;
    left: 0;
  }

  &__date {
    font-size: 20px;
    font-weight: 700;
    line-height: 1.2;
  }

  &__title {
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    color: $secondary;
    margin-bottom: 12px;
  }

  &__desc {
    @include max-line(3);
    margin-bottom: 16px;
  }

  &__link {
    display: inline-block;
    font-size: 14px;
    line-height: 30px;
    padding: 0 16px;
    border-left: 4px solid $primary;
    color: #333;

    &:hover {
      color: #333;
      text-decoration: none;
      background-color: $light;
    }
  }
}

.product {
  &__frame {
    @include frame('shine');
    display: block;
    position: relative;
    border: 1px solid $border-color;
    border-bottom: 0;
    overflow: hidden;

    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-bottom: 75%;
    }

    img {
      display: block;
      width: calc(100% - 32px);
      height: calc(100% - 32px);
      object-fit: contain;
      object-position: 50% 50%;
      position: absolute;
      top: 16px;
      left: 16px;
      transition: 0.4s;
    }

    &:hover img {
      transform: scale(1.05);
    }
  }

  &__title {
    font-size: 14px;
    color: $white;
    background-color: $secondary;
    padding: 10px 16px;
    margin-bottom: 0;

    a:hover {
      color: $white;
    }
  }
}

.product-2 {
  display: flex;
  align-items: center;
  height: 100%;
  border: 1px solid $border-color;
  padding: 10px 10px 10px 28px;
  position: relative;
  z-index: 1;
  color: #333;

  &:hover {
    color: $primary;
    text-decoration: none;
  }

  &::before {
    content: '';
    display: block;
    width: 15px;
    background-color: $primary;
    position: absolute;
    top: 2px;
    left: 2px;
    bottom: 2px;
  }

  &::after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, rgba($light, 0.5), transparent);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: 0.4s;
    opacity: 0;
  }

  &:hover::after {
    opacity: 1;
  }

  &__title {
    flex: 1;
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 0;
    position: relative;
    z-index: 10;
  }

  &__frame {
    @include frame('square');
    width: 70px;
    margin-left: -10px;

    img {
      object-fit: contain;
      object-position: 50% 50%;
    }
  }
}
