.footer {
  background-color: $light;
  padding: 40px 0 0;

  &__title {
    font-size: 16px;
    font-weight: 700;
    color: #333;
    margin-bottom: 24px;
  }

  &__content {
    font-size: 12px;
    line-height: 30px;
    color: #333;
  }
}

.endpage {
  background-color: $primary;
  padding: 8px 0;

  &__inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__title {
    font-size: 16px;
    text-transform: uppercase;
    color: $white;
    margin-right: 30px;
  }

  &__nav {
    display: flex;
    align-items: center;
  }

  &__nav-item {
    display: inline-block;
    color: rgba(#fff, 0.8);

    &:hover {
      color: $white;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 40px;
      position: relative;

      &::after {
        content: '';
        display: block;
        height: 12px;
        border-right: 1px solid rgba(#fff, 0.6);
        position: absolute;
        top: 50%;
        right: -20px;
        transform: translateY(-50%);
      }
    }
  }

  &__copyright {
    color: $white;
    margin-left: auto;
  }

  @media (max-width: 1199px) {
    &__inner {
      flex-direction: column;
      text-align: center;
    }

    &__title {
      margin-right: 0;
      margin-bottom: 4px;
    }

    &__copyright {
      margin-left: 0;
      margin-top: 4px;
    }
  }
}

.sitemap {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
  font-size: 12px;
  line-height: 30px;
  color: #333;

  &__item {
    padding-left: 12px;
    position: relative;

    &::before {
      content: '';
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: currentColor;
      position: absolute;
      top: 12px;
      left: 0;
    }
  }

  &__link {
    color: inherit;

    &:hover {
      color: $secondary;
    }

    &+& {
      display: inline-block;
      padding-left: 16px;
      margin-left: 16px;
      position: relative;

      &::before {
        content: '';
        display: block;
        width: 10px;
        border-bottom: 1px solid currentColor;
        position: absolute;
        top: 14px;
        left: 0;
      }
    }
  }

  &__sub {
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
  }

  &__sub-item {
    padding-left: 0;
    position: relative;

    &::before {
      content: '';
      display: block;
      width: 6px;
      height: 6px;
      border-left: 1px solid currentColor;
      border-bottom: 1px solid currentColor;
      position: absolute;
      top: 8px;
      left: -12px;
    }
  }

  &__sub-link {
    color: inherit;

    &:hover {
      color: $secondary;
    }
  }
}
