.section {
  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    border-bottom: 1px solid $border-color;
    margin-bottom: 30px;
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    color: #333;
    border-bottom: 4px solid $border-color;
    padding-bottom: 5px;
    margin-bottom: 0;
  }

  &__more {
    display: inline-block;
    color: #666;
    margin-left: auto;

    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }

  @media (max-width: 767px) {
    &__header {
      margin-bottom: 20px;
    }

    &__title {
      font-size: 16px;
      border-bottom-width: 2px;
    }
  }
}

.section-2 {
  margin-bottom: 20px;

  &__title {
    font-size: 14px;
    font-weight: 700;
    background-color: $light;
    color: $secondary;
    padding: 7px 16px;
    margin-bottom: 16px;
  }
}

.aside {
  padding: 0 4px;
  border: 1px solid $border-color;

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  &__title {
    border-bottom: 1px solid $border-color;
    font-size: 14px;
    font-weight: 700;
    color: $primary;
    padding: 10px;
    margin-bottom: 0;
  }

  &__body {
    padding: 16px 12px;
  }
}
