.preview {
  display: grid;
  grid-template-columns: calc(100% - 212px) 197px;
  grid-template-rows: auto;
  grid-gap: 16px;
  grid-auto-flow: row dense;
  background-color: $light;
  padding: 15px;
  margin-bottom: 20px;

  &__main {
    grid-column: 1 / span 1;
  }

  &__thumbs {
    grid-column: 2 / span 1;
    grid-row: 1 / span 1;
    position: relative;
  }

  @media (max-width: 1199px) {
    grid-template-columns: calc(100% - 147px) 137px;
    grid-gap: 10px;
    padding: 10px;
  }

  @media (max-width: 575px) {
    grid-template-columns: calc(100% - 80px) 70px;
  }
}

.preview-slider {
  &__frame {
    @include frame('rect', 60%);
    background-color: $white;

    img {
      width: calc(100% - 40px);
      height: calc(100% - 40px);
      object-fit: contain;
      top: 20px;
      left: 20px;
    }
  }

  @media (max-width: 1199px) {
    &__frame {
      img {
        width: calc(100% - 8px);
        height: calc(100% - 8px);
        top: 4px;
        left: 4px;
      }
    }
  }
}

.thumb-slider {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;

  &__pagination {
    width: 3px;
    height: 100%;
    background-color: rgba(#000, 0.2);
    position: absolute;
    top: 0;
    right: -9px;

    .swiper-pagination-progressbar-fill {
      background-color: $primary;
    }
  }

  &:not(.has-nav) &__pagination {
    display: none;
  }

  &__frame {
    @include frame('rect', 60%);
    background-color: $white;
    object-fit: contain;

    img {
      width: calc(100% - 40px);
      height: calc(100% - 40px);
      top: 20px;
      left: 20px;
    }
  }

  &__prev,
  &__next {
    display: none;
  }

  .swiper-container {
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    height: auto !important;
  }

  @media (max-width: 1199px) {
    &__frame {
      img {
        width: calc(100% - 8px);
        height: calc(100% - 8px);
        top: 4px;
        left: 4px;
      }
    }
  }
}
