.header {
  position: relative;
  z-index: 100;

  &__inner {
    display: flex;
    align-items: center;
    position: relative;
  }

  &__elements {
    display: flex;
    align-items: center;
    margin-left: auto;
  }

  &__btn {
    display: inline-flex;
    width: 35px;
    height: 35px;
    justify-content: center;
    align-items: center;
    position: relative;
    color: $gray-800;
    font-size: 20px;

    &+& {
      margin-left: 4px;
    }

    &:hover {
      color: $primary;
      text-decoration: none;
    }

    span {
      display: inline-flex;
      width: 15px;
      height: 15px;
      justify-content: center;
      align-items: center;
      background-color: $primary;
      border-radius: 50%;
      font-size: 11px;
      font-weight: 700;
      text-align: center;
      color: $white;
      position: absolute;
      top: 0;
      right: 0;
    }

    img {
      max-height: 16px;
      object-fit: contain;
      object-position: 50% 50%;
    }
  }

  @media (min-width: 1200px) {
    &__btn {
      display: none;
    }
  }
}

.logo {
  display: inline-flex;
  margin: 10px 0;

  img {
    height: 50px;
  }

  @media (min-width: 1200px) {
    margin: 18px 0;

    img {
      height: 64px;
    }
  }
}

.search-btn {
  display: none;

  @media (min-width: 1200px) {
    display: inline-flex;
    width: 35px;
    height: 35px;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    border: 1px solid #333;
    color: #333;
    background: 0;
    margin-left: 10px;

    &:hover {
      background-color: $light;
    }
  }
}

.search {
  border-bottom: 1px solid $border-color;
  padding: 10px 20px 20px;
  margin-bottom: 10px;

  @media (min-width: 1200px) {
    display: none;
    width: 300px;
    padding: 0;
    margin: 0;
    border: 0;
    position: absolute;
    top: 100%;
    right: 0;
  }
}
