.home-banners {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  margin-top: 20px;

  &__frame {
    display: block;
    @include frame('shine');

    img {
      display: block;
      width: 100%;
    }
  }

  @media (max-width: 767px) {
    grid-gap: 8px;
    margin-top: 8px;
  }
}

.as-menu {
  list-style: none;
  padding-left: 0;
  margin-bottom: 20px;

  &__item {
    &:not(:last-child) {
      border-bottom: 1px solid $white;
    }
  }

  &__link {
    display: block;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    padding: 10px 16px;
    color: $white;
    background-color: #666666;

    &:hover {
      color: $white;
      text-decoration: none;
      background-color: #555;
    }
  }

  &__sub {
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
  }

  &__sub-item {
    border-top: 1px solid $white;
  }

  &__sub-link {
    display: block;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    padding: 10px 16px;
    color: $white;
    background-color: $primary;

    &:hover {
      color: $white;
      text-decoration: none;
      background-color: darken($primary, 5%);
    }

    &.active {
      position: relative;

      &::before {
        content: '';
        display: block;
        border: 6px solid transparent;
        border-left-color: $white;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
    }
  }

  &__sub-2 {
    display: none;
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;

    &.active {
      display: block;
    }
  }

  // &__sub-link.active~&__sub-2 {
  //   display: block;
  // }

  &__sub-2-item {
    border-top: 1px solid $white;
  }

  &__sub-2-link {
    display: block;
    font-size: 14px;
    line-height: 20px;
    padding: 10px 16px;
    color: #333;
    background-color: $light;

    &:hover {
      color: #333;
      text-decoration: none;
      background-color: darken($light, 5%);
    }

    // &.active {
    //   position: relative;
    //   background-color: $primary;
    //   color: $white;
    //   font-weight: 700;

    //   &:hover {
    //     color: $white;
    //     background-color: darken($primary, 5%);
    //   }

    //   &::before {
    //     content: '';
    //     display: block;
    //     border: 6px solid transparent;
    //     border-left-color: $white;
    //     position: absolute;
    //     top: 50%;
    //     left: 0;
    //     transform: translateY(-50%);
    //   }
    // }
  }
}

.as-list {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;

  &__item {
    padding-left: 18px;
    position: relative;

    &::before {
      content: '';
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background-color: $primary;
      position: absolute;
      top: 8px;
      left: 0;
    }

    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }
}

.as-support {
  &__frame {
    width: 110px;
    margin-right: 10px;
  }

  &__label {
    font-weight: 700;
    margin-bottom: 8px;
  }

  &__nav {
    display: flex;
    margin-top: 10px;
  }

  &__nav-item {
    display: flex;

    &:not(:last-child) {
      margin-right: 10px;
    }

    img {
      height: 25px;
    }
  }
}

.aside-map {
  height: 250px;
}

.product-nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 30px;

  &__item {
    padding: 20px 8px;
    border-top: 1px solid $border-color;

    &:first-child {
      border-right: 1px solid $border-color;
    }
  }

  &__link {
    align-items: center;
    color: #333;

    &:hover {
      color: $primary;
      text-decoration: none;
    }

    .fa {
      position: relative;
      top: -1px;
    }
  }

  &__item:last-child {
    text-align: right;
  }
}

.product-title {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 16px;
}

.about {
  &__title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 20px;
  }
}

.post {
  &-title {
    font-size: 18px;
    font-weight: 700;
  }

  &-time {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 67px;
    height: 60px;
    background-color: $primary;
    color: $white;
    margin-right: 15px;
  }

  &-date {
    font-size: 20px;
    font-weight: 700;
    line-height: 1.2;
  }

  &-sapo {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 16px;
  }
}

.download-table {
  border-bottom: 1px solid $border-color;

  &__name {
    min-width: 130px;
  }

  tbody {
    tr:hover {
      background-color: $light;
    }

    td {
      vertical-align: middle;
    }
  }
}

.contact-info {
  font-size: 14px;
  line-height: 30px;
  font-weight: 700;
}

.contact-form {
  background-color: $light;
  border: 1px solid $border-color;
  padding: 20px;

  &__btn {
    text-transform: uppercase;
    font-weight: 700;
    border-radius: 0;
    padding-left: 80px;
    padding-right: 80px;
  }

  .form-control {
    border-radius: 0;
  }
}
