.banner-slider {
  position: relative;
  z-index: 1;

  &__prev,
  &__next {
    display: flex;
    width: 5%;
    height: 100%;
    justify-content: center;
    align-items: center;
    color: $white;
    background: linear-gradient(to right, rgba(#000, 0.3), transparent);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    cursor: pointer;
    font-size: 20px;

    &:focus {
      outline: 0;
    }
  }

  &__next {
    left: auto;
    right: 0;
    background: linear-gradient(to left, rgba(#000, 0.3), transparent);
  }

  @media (min-width: 1200px) {

    &__prev,
    &__next {
      opacity: 0;
      transition: 0.4s;
    }

    &:hover &__prev,
    &:hover &__next {
      opacity: 1;
    }
  }
}
